"use client";
import { useEffect, useState } from "react";
import { useAuth } from "../../context/AuthContext";
import { useAuthGuard } from "../../hooks/useAuthGuard";
import { Modal } from "bootstrap";
import api from "../../services/api";

interface Employee {
	id: number;
	firstName: string;
	lastName: string;
	rate: number;
	workDays: number;
}

export default function EmployeesPage() {
	const { token } = useAuth();
	const [employees, setEmployees] = useState<Employee[]>([]);
	const [modalEmployee, setModalEmployee] = useState<Employee | null>(null);
	const [isEdit, setIsEdit] = useState(false);
	const { user, loading } = useAuthGuard();
	const [showModal, setShowModal] = useState(false);

	const fetchEmployees = async () => {
		if (!token) return;
		try {
			const res = await api.get("/api/employees");
			setEmployees(res.data);
		} catch (err) {
			console.log("Failed to fetch employees");
		}
	};

	useEffect(() => {
	if (token) {
		fetchEmployees();
	}
	}, [token]);

	const handleSubmit = async (e: React.FormEvent) => {
		e.preventDefault();
		if (!modalEmployee) return;

	try {
		let res;
		if (isEdit) {
			res = await api.put(`/api/employees/${modalEmployee.id}`, modalEmployee);
		} else {
			res = await api.post("/api/employees", modalEmployee);
		}

		if (res.status === 200 || res.status === 201) {
			setModalEmployee(null);
			fetchEmployees();

			const modalEl = document.getElementById("employeeModal");
			if (modalEl) {
				const modal = Modal.getOrCreateInstance(modalEl);
				modal.hide();
			}
		} else {
			console.log("Something unexpected happened");
		}
	} catch (err) {
	console.error(err);
	console.log("Failed to save employee");
	}
	};

	const handleDelete = async (id: number) => {
		if (!confirm("Are you sure?")) return;
		try {
			await api.delete(`/api/employees/${id}`);
			fetchEmployees();
		} catch (err) {
			console.log("Failed to delete employee");
		}
	};

	if (loading) return <p>Checking authentication...</p>;
	if (!user) return <p>Redirecting...</p>;

return (
	<div className="container py-5">
		<h2>Employees</h2>
<button
  className="btn btn-primary mb-3"
  onClick={() => {
    setIsEdit(false);
    setModalEmployee({ id: 0, firstName: "", lastName: "", rate: 0, workDays: 0 });

    const modalEl = document.getElementById("employeeModal");
    if (modalEl) {
      const modal = (window as any).bootstrap.Modal.getOrCreateInstance(modalEl);
      modal.show(); // âœ… show via JS only
    }
  }}
>
  Add Employee
</button>		<table className="table table-striped">
			<thead>
				<tr>
					<th>Name</th>
					<th>Last Name</th>
					<th>Rate</th>
					<th>Work Days</th>
					<th>Actions</th>
				</tr>
			</thead>
			<tbody>
				{employees.map((emp) => (
					<tr key={emp.id}>
						<td>{emp.firstName}</td>
						<td>{emp.lastName}</td>
						<td>{emp.rate}</td>
						<td>{emp.workDays}</td>
						<td>
							<button
							className="btn btn-primary me-2"
							data-bs-toggle="modal"
							data-bs-target="#employeeModal"
							onClick={() => {
								setModalEmployee(emp);
								setIsEdit(true);
							}}
							>
							Edit
							</button>
							<button className="btn btn-danger" onClick={() => handleDelete(emp.id)}>
							Delete
							</button>
						</td>
					</tr>
				))}
			</tbody>
		</table>

		{/* Modal */}
		<div className="modal fade" id="employeeModal" tabIndex={-1} aria-hidden="true">
			<div className="modal-dialog">
				<form className="modal-content" onSubmit={handleSubmit}>
					<div className="modal-header">
						<h5 className="modal-title">{isEdit ? "Edit" : "Add"} Employee</h5>
						<button type="button" className="btn-close" data-bs-dismiss="modal"></button>
					</div>
					<div className="modal-body">
						<input
							type="text"
							className="form-control mb-2"
							placeholder="Name"
							value={modalEmployee?.firstName || ""}
							onChange={(e) => setModalEmployee((prev) => ({ ...prev!, firstName: e.target.value }))}
							required
						/>
						<input
							type="text"
							className="form-control mb-2"
							placeholder="Last Name"
							value={modalEmployee?.lastName || ""}
							onChange={(e) => setModalEmployee((prev) => ({ ...prev!, lastName: e.target.value }))}
							required
						/>
						<input
							type="number"
							className="form-control mb-2"
							placeholder="Rate"
							value={modalEmployee?.rate || 0}
							onChange={(e) => setModalEmployee((prev) => ({ ...prev!, rate: Number(e.target.value) }))}
							required
						/>
						<input
							type="number"
							className="form-control mb-2"
							placeholder="Work Days"
							value={modalEmployee?.workDays || 0}
							onChange={(e) => setModalEmployee((prev) => ({ ...prev!, workDays: Number(e.target.value) }))}
							required
						/>
					</div>
					<div className="modal-footer">
						<button type="button" className="btn btn-secondary" data-bs-dismiss="modal">
							Close
						</button>
						<button type="submit" className="btn btn-primary">
							{isEdit ? "Save Changes" : "Add Employee"}
						</button>
					</div>
				</form>
			</div>
		</div>
	</div>
);
}
